import * as Yup from 'yup';
import useLocale from '../hooks/locale'
const locale = useLocale()

const PaymentMethodSchema = Yup.object().shape({
    name: Yup.string()
        .min(2, 'Too Short!')
        .max(50, 'Too Long!')
        .required('Required'),
    career: Yup.number()
        .required('Required'),
    birthdate: Yup.date()
        .required('Required'),
    country: Yup.number()
        .required('Required'),
    city: Yup.string()
        .required('Required'),
    phone: Yup.string()
        .required('Required'),
    email: Yup.string()
        .email('Invalid email')
        .required('Required'),
})

export { PaymentMethodSchema }